import React, { useEffect, useState } from 'react'
import Axios from 'axios';
import StoreNav from './StoreNav'
const jwt = require("jsonwebtoken");

const StoreCampaings = () => {
  const [campaigns, setCampaigns] = useState([]);
  const [categories, setCategories] = useState();

  const sessionID = null || localStorage.getItem('sessionID') || sessionStorage.getItem('sessionID');
  const user = jwt.verify(sessionID, 'shhhhh');

  const getCampaigns = async () => {
    const res = await Axios({
      method: "GET",
      data: {
        user_id: user.user_id,
        role_id: user.role_id
      },
      withCredentials: true,
      url: `http://localhost:4000/getCampaigns`,
    });
    setCampaigns(res.data);
  }

  const getItems = async () => {
    const res = await Axios({
      method: "POST",
      data: {
        user_id: user.user_id,
        role_id: user.role_id
      },
      withCredentials: true,
      url: `/getStoreItems`,
    });
    console.log(res.data);
    let productCategories = new Map();
    for(let item of res.data){
      if(productCategories.get(item.category) === undefined)
        productCategories.set(item.category,[item])
      else
        productCategories.set(item.category, [...productCategories.get(item.category), item])
    }
    console.log(productCategories);
    console.log(productCategories.keys());
    console.log(Array.from(productCategories.keys()));
    setCategories(productCategories);
  }

  useEffect(() => {
    getCampaigns();
    getItems();

  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
  }, [campaigns])


  return (
    <div>
      <StoreNav user={user} />

      <h3 style={{textAlign: "center"}}>Campaigns</h3>
      <div style={{border: "1px solid red", width: "70%", margin: "40px auto", padding: "20px"}}>
        {
          (campaigns.length === 0) ? <p>Store does not have any campaign</p>: <span>campaign</span>
        }
      </div>
      <h3 style={{textAlign: "center"}}>Deploy Campaign</h3>
      <div style={{border: "1px solid red", width: "70%", margin: "40px auto", padding: "20px"}}>
          <h5>Campaign by Category</h5>
        <div>
          <label>Category</label>
          <select style={{width: "120px"}}>
            {
              /* Array.from(categories.keys()).map( category => {
                return <option value={category}>{category}</option>
              }) */
            }
            
          </select>
          <label style={{margin: "auto"}}>Discount</label>
          <input type="text" value="%" style={{width: "70px"}}/>

          <label style={{margin: "auto"}}>Date By</label>
          <input type="date" id="date"/>

          <input type="button" value="Deploy" id="category"/>
        </div>
        
      </div>
    </div>
  )
}

export default StoreCampaings
